import transDate from "@/utils/transDate";
export default {
  data: function data() {
    return {};
  },
  props: {
    item: Object,
    type: String
  },
  computed: {
    commentTime: function commentTime() {
      return function (time) {
        var date = transDate(time);
        return "".concat(date.year, "\u5E74").concat(date.month, "\u6708").concat(date.day, "\u65E5");
      };
    },
    black: function black() {
      return this.type === "song";
    }
  },
  methods: {
    gotoUser: function gotoUser(id) {
      this.$router.push({
        path: "/user/playlist",
        query: {
          uid: id
        }
      });
    },
    download: function download() {
      this.$router.push({
        path: "/download"
      });
    }
  }
};